.container__nav, .container__nav--menu {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.container__nav--menu {
  gap: 2.5rem;
  margin-top: .125rem;
}

.container__header {
  height: 14.375rem;
  color: #fff;
  max-width: 320px;
  flex-direction: column;
  gap: 3.375rem;
  padding: 2.5rem 1.25rem 5.75rem;
  display: flex;
}

.container__background {
  background-image: url("abstract-bg-mobile.96f3b14b.png");
  background-repeat: no-repeat;
  background-size: 100% 14.375rem;
}

.logo {
  display: none;
}

.logo__icon {
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.logo__box:hover, .logo__box:focus {
  color: #ff6b08;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.logo__box:hover .logo__icon, .logo__box:focus .logo__icon {
  background-color: #ff6b08;
  border-radius: 5px;
}

@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .container__background {
    background-image: url("abstract-bg-mobile@2x.8bb34ae7.png");
    background-size: 100% 14.375rem;
  }
}

@media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
  .container__background {
    background-image: url("abstract-bg-mobile@3x.d0e71174.png");
    background-size: 100% 14.375rem;
  }
}

.main-nav {
  text-transform: uppercase;
  font-size: .75rem;
  font-weight: 500;
  line-height: .875rem;
  display: block;
}

.main-nav:hover, .main-nav:focus {
  color: #ff6b08;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.main-nav__is-active {
  position: relative;
}

.main-nav__is-active:after {
  width: 100%;
  height: 3px;
  content: "";
  background-color: #ff001b;
  display: block;
  position: absolute;
  bottom: -3px;
}

.search__box {
  width: 100%;
  position: relative;
}

.search__input {
  width: 100%;
  color: #fff;
  background-color: #0000;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  margin-bottom: 1rem;
  padding-bottom: .25rem;
  font-size: .875rem;
  line-height: 1rem;
}

.search__input::placeholder {
  color: #fff;
}

.search__icon {
  cursor: pointer;
  background: none;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: .25rem;
  right: 0;
}

.search__icon:hover {
  background-color: #ff6b08;
  border-radius: 50%;
}

.search__not-found {
  color: #ff001b;
  visibility: hidden;
  font-size: .875rem;
  line-height: 1rem;
}

@media (min-width: 768px) {
  .container__header {
    height: 13.5rem;
    max-width: 768px;
    gap: 2.5rem;
    padding-bottom: 5.063rem;
  }

  .container__background {
    background-image: url("abstract-bg-tablet.74c32281.png");
    background-size: 100% 13.5rem;
  }

  .logo {
    font-size: 1.875rem;
    font-weight: 500;
    line-height: 2.188rem;
    display: inline-block;
  }

  .logo__box {
    align-items: center;
    gap: .5rem;
    display: flex;
  }

  .search__box {
    flex-direction: column;
    align-items: center;
    display: flex;
  }

  .search__input {
    width: 21rem;
    margin-bottom: 1.25rem;
  }

  .search__icon {
    right: calc(50% - 10.5rem);
  }
}

