.backdrop {
  width: 100%;
  height: 100%;
  z-index: 3;
  background-color: #0003;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.hidden {
  display: none;
}

.modal {
  background-color: var(--text-white);
  padding-inline: 1.25rem;
  max-width: 280px;
  max-height: 100vh;
  border-radius: 3px;
  flex-direction: column;
  padding-top: 3rem;
  padding-bottom: 2rem;
  display: flex;
  position: fixed;
  top: 20px;
  left: 50%;
  overflow-y: scroll;
  transform: translate(-50%);
}

.modal__btn--close {
  background-color: var(--text-white);
  width: 1.5rem;
  height: 1.5rem;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  display: flex;
  position: absolute;
  top: .75rem;
  right: .75rem;
}

.modal__btn--watched {
  text-align: center;
  text-transform: uppercase;
  color: var(--text-white);
  height: 2.75rem;
  width: 6.875rem;
  cursor: pointer;
  background: #ff6b01;
  border-radius: 5px;
  padding-inline: .3rem;
  font-family: inherit;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  transition-duration: .4s;
}

.modal__btn--watched:hover {
  box-shadow: 0 12px 16px #0000003d, 0 17px 50px #00000030;
}

.modal__btn--watched:active {
  transform: translateY(2px);
}

.modal__btn--queued {
  background: var(--text-white);
  border: 1px solid var(--text-black);
  text-align: center;
  text-transform: uppercase;
  height: 2.75rem;
  width: 6.875rem;
  cursor: pointer;
  border-radius: 5px;
  padding-inline: .3rem;
  font-family: inherit;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  transition-duration: .4s;
}

.modal__btn--queued:hover {
  box-shadow: 0 12px 16px #0000003d, 0 17px 50px #00000030;
}

.modal__btn--queued:active {
  transform: translateY(2px);
}

.modal__image {
